@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:2rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Raleway';
}
.App
{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  padding:60px 30px 0px 30px ;
  flex:1.5 1;
  flex:var(--sidebar-flex) 1;
  background: #1f2036;
  background: linear-gradient(180deg, rgba(39,40,69,1) 40%);
}
.condiv
{
  padding: 100px ;
  background-color: #f4f5f9;
  flex:6 1;
  flex:var(--condiv-flex) 1;
}

.home
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  
}
.profilepic
{
  position: absolute;
  object-fit: cover;
  top: 25%;
  left: 60%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 60%;
  width: 12%;
  margin: 30px;
}
.typingeffect
{
  position: relative;
  align-content: center;
  text-align: center;
  font-size: 2rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
  top: 40%;
  width: 10%;
}

nav ul{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li
{
  margin:40px;
}

a
{
  text-decoration: none;
  color: white;
}

.subtopic
{
  margin:10px 10px 30px 10px !important;
}
p{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.social
{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-left: 45px;
  margin-right: 0px;
  /*padding-left: 20px ;*/
  /*padding-right: 25px;*/
  bottom:60px;
}

a.social {
  margin: 0 1rem;
  /*justify-content: center;
  align-self:center;*/
  margin-left: 5pt;
  margin-right: 5pt;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
  display:flex;
  flex-direction: column;
}

a.social:hover {
  -webkit-transform: translateY(-15px);
          transform: translateY(-15px);
}

a.twitter {
  color: #49a1eb;
  padding: 5px;
}
a.github{
  display: flex;
  color: black;
  padding: 5px;
}
a.linkedin{
  display: flex;
  color: #0e76a8;
  padding: 5px;
}
a.hackerrank{
  display: flex;
  color: #04b831;
  padding: 5px;
}

.widecard
{
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    flex-direction:column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(254,106,74);
    background: linear-gradient(180deg, rgba(238,103,143,1) 50%);
  }
  .condiv
  {
    padding: 50px ;
  }
  nav ul
  {
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    display: flex;
    /*justify-content: center;*/
    flex-direction: row;
    bottom: 30px;
  } 
  /* .social icon 
  {
    padding-right: 0px;
  } */
}



